  const [guestOf, setGuestOf] = useState();
  const [inviteInfo, setInviteInfo] = useState();
  const [modalShow, setModalShow] = useState(false);

  useEffect(() => {
    const url = new URL(window.location.href);
    console.log(url);
    let idx = -1
    let guestOf = ""
    if (url.pathname && url.pathname.slice(1)) {
      idx = url.pathname.slice(2);
      guestOf =
      url.pathname && url.pathname.startsWith("/b")
        ? "boy"
        : url.pathname && url.pathname.startsWith("/g")
        ? "girl"
        : "";
    } else if (url.searchParams) {
      const nameB = url.searchParams.get("b");
      if (nameB) {
        idx = nameB
        guestOf = "boy"
      }
      const nameG = url.searchParams.get("g");
      if (nameG) {
        idx = nameG
        guestOf = "girl"
      }
    }

    if (!guestOf) return;
    setGuestOf(guestOf);

    if (idx != -1 && guests["b"][idx - 1]) {
      const guest = guests["b"][idx - 1];
      console.log(guest);
      setName(guest.name);
      setInviteInfo(guest);
    }

    // const originalText =
    //   "b1";
    // let encryptedText1 = encryptText(originalText);
    // console.log("Chui  m ha:", encryptedText1);
    // const encryptedText = encrypt(originalText);
    // console.log("M ha:", encryptedText);

    // const decryptedText = decrypt(encryptedText);
    // console.log("Gii m:", decryptedText);
  }, []);

